@import "../details/mixin";

.mydata,
.about-content,
.list,
.list li {
    @include flexcc
}

.languages,
.about-main, .course-content, .course-content .block, .about-container {
    @include flexCCC;
}

.about-content,
.course-content,
.languages {
    @include glassEffect;
}

.about-main {

    .about-container {
        gap: 2em;
        padding-top: 2em;

        .about-content {
            width: 80%;
            text-align: center;

            p {
                font-size: 2em;
            }
        }

        .course-content {
            font-size: 1.5em;
            gap: 1.5em;
        }

        .languages {
            width: 70%;

            .list {
                font-size: 1.5em;
            }
        }


        .list {
            list-style: none;
            gap: 1.5em;
            flex-wrap: wrap;
            font-size: 1.2em;
        }

        .mydata {
            width: 100%;

            ul {
                width: 100%;
                gap: 1.5em;

                li {
                    align-items: center;
                    font-size: 1.2em;
                    gap: .6em;
                    height: 44px;
                    text-overflow: ellipsis;
                    @include glassEffect;


                    a {
                        color: #fff;
                        display: flex;
                        gap: 0.6em;
                    }

                    img {
                        width: 30px;
                        color: #fff;
                    }
                }
            }
        }
    }


}